import { DatePipe } from '@angular/common';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { Appointment } from '../appointment/masters/appointment';
import { AppointmentService } from '../appointment/services/appointment.service';
import { Consultant } from '../encounter/masters/consultant';
import { Encounter } from '../encounter/masters/encounter';
import { ConsultantService } from '../encounter/services/consultant.service';
import { EncounterService } from '../encounter/services/encounter.service';
import { Department } from '../master/department/masters/department';
import { DepartmentService } from '../master/department/services/department.service';
import { LookupEntity } from '../master/lookup-module/masters/lookup-entity';
import { LookupService } from '../master/lookup-module/services/lookup.service';
import { ServiceCenter } from '../master/service-center/masters/service-center';
import { BranchServiceService } from '../master/service-center/services/branch-service.service';
import { Employee } from '../master/unit-master/masters/Employee';
import { EmployeeService } from '../master/unit-master/services/employee-master.service';
import { routerTransition } from '../router.animations';

@Component({
	selector: 'app-nurseworkbench',
	templateUrl: './nurseworkbench.component.html',
	styleUrls: ['./nurseworkbench.component.css'],
	animations: [routerTransition()]
})
export class NurseworkbenchComponent implements OnInit {

	departmentList: Array<Department>;
	departmentId: number;
	department: Department = new Department();
	consultantList: Array<Consultant>;
	consultantId: number;
	consultant: Consultant = new Consultant();
	serviceCenterList: Array<ServiceCenter>;
	serviceCenterId: number;
	serviceCenter: ServiceCenter = new ServiceCenter();
	lookupEntityList: Array<LookupEntity> = new Array<LookupEntity>();
	appointmentList: Array<Appointment>;
	appointment: Appointment = new Appointment();
	appointmentId: number;
	employee: Employee = new Employee();
	employeeId: number;
	employeeList: [];
	encounterList: Array<Encounter> = [];
	encounter: Encounter = new Encounter();
	encounterId: number;
	showImage: boolean = false;
	visitDateFrom: any;
	visitDateTo: any;
	isSearched: boolean = false;
	mrn: any;
	patientName: string;
	date: Date = new Date();
	@Output() checkedInByDateSearchEncounterList: Array<Encounter> = [];
	departmentForSearch: number = null;
	serviceCenterForSearch: number = null;
	encounterStatusForSearch: number = 183;
	visitTypeForSearch: number = null;
	@Input() isNurseWorkbench : boolean = true;
	constructor(private departmentService: DepartmentService,
		public lookupService: LookupService,
		public appointmentService: AppointmentService,
		public encounterService: EncounterService,
		public serviceCenterService: BranchServiceService,
		public consultantService: ConsultantService,
		public employeeService: EmployeeService,
		private router: Router,
		private datePipe: DatePipe) { }

	ngOnInit(): void {
		this.reloadData();
		this.visitDateFrom = this.datePipe.transform(this.date, 'yyyy-MM-dd');
		this.visitDateTo = this.datePipe.transform(this.date, 'yyyy-MM-dd');
	}
	reloadData() {
		this.departmentService.getAllDepartments().subscribe(data => {
			this.departmentList = data;
			//console.log(data)

		}),
			this.serviceCenterService.getServiceCenter().subscribe(data => {
				this.serviceCenterList = data;
				//console.log(data)

			}),
			this.consultantService.getConsultantList().subscribe(data => {
				this.consultantList = data;


			}),
			this.lookupService.getAllLookupEntities().subscribe(data => {
				this.lookupEntityList = data;
			}),

			this.appointmentService.getAppointmentList().subscribe(data => {
				this.appointmentList = data;

			}),
			this.employeeService.getEmployee().subscribe(data => {
				this.employeeList = data;

			}),
			this.encounterService.getEncounter().subscribe(data => {
				this.encounterList = data;
				//console.log(this.encounterList)

			})
		setTimeout(() => {
			for (let i of this.encounterList) {
				//console.log(i)
			}
		}, 3000);
		//console.log(this.encounter.encounterTime);

	}
	showServiceCenter(event) {
		this.departmentId = event;
		for (let department of this.departmentList) {
			if (department.departmentId == this.departmentId) {
				this.department = department;
			}

		}

	}
	showConsultant(event) {
		this.serviceCenterId = event;
		for (let serviceCenter of this.serviceCenterList) {
			if (serviceCenter.serviceCenterId == this.serviceCenterId) {
				this.serviceCenter = serviceCenter;
			}
		}

	}
	encounterTime(appointment) {

		this.encounterService.getEncounterByAppointment(appointment).subscribe(data => {
			return data;
		})
		return "not-found";
	}
	showpatientdetails(encounter) {
		sessionStorage.setItem('patientId', encounter.appointment.patient.patientId)
		this.router.navigate(['/nurseworkbench/consultation', encounter.encounterId])

	}
	showCheckin(encounter) {
		//console.log(encounter);
	}
	gotoallergyrecording(encounter) {
		sessionStorage.setItem('patientId', encounter.appointment.patient.patientId)
		sessionStorage.setItem('encounterId', encounter.encounterId)
		this.router.navigate(["/nurseworkbench/allergy-recording"])
		//console.log(this.encounter.appointment.patient.patientId);

	}
	searchByDateRange() {
		this.checkedInByDateSearchEncounterList = [];

		for (let encounter of this.encounterList) {
			let date = new Date(encounter.encounterDate);
			let encounterDate: number = date.getTime();
			let fromDate = new Date(this.visitDateFrom);
			let toDate = new Date(this.visitDateTo);
			if (fromDate.getTime() < encounterDate && encounterDate < toDate.getTime()) {
				this.checkedInByDateSearchEncounterList.push(encounter)

			}
		}
		this.isSearched = true;
	}
	showEncounterStatus(value) {
		console.log(this.encounterStatusForSearch)
	}

}
