import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { NurseworkbenchComponent } from './nurseworkbench.component';

const routes: Routes = [
	{path: '', component : NurseworkbenchComponent},
	{path: 'startConsultation/:id', loadChildren: () => import('./../clinical/consultation/consultation.module').then((m) => m.ConsultationModule)},
	{path: 'allergyRecording', loadChildren: () => import('./../clinical/allergy-recording/allergy-recording.module').then((m) => m.AllergyRecordingModule)},
	{path: 'vitalSign', loadChildren: () => import('../clinical/vital-sign/vital-sign.module').then((m) => m.VitalSignModule)},
	{path: 'clinicalCoordinatorNotes', loadChildren: () => import('../clinical/clinical-coordinator-notes/clinical-coordinator-notes.module').then((m) => m.ClinicalCoordinatorNotesModule)},
	{path: 'patientDocument',loadChildren: () => import('.././clinical/patient-document/patient-document.module').then((m) => m.PatientDocumentModule)},
	{path: 'clinicalDetails', loadChildren: () => import('.././clinical/clinical-details/clinical-details.module').then((m) =>m.ClinicalDetailsModule)},
	
	{path: 'orders',loadChildren: () => import('.././clinical/orders/orders.module').then((m) => m.OrdersModule)}
	
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NurseworkbenchRoutingModule { }
